<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  //tag1-start
  const defaultEchdata = {
    xData: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
    yData: [150, 230, 224, 218, 135, 147, 260]
  };
  //tag1-end
  //tag1B-start
  const defaultTitle = '标题';
  //tag1B-end
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: defaultTitle
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start
        let echartEle = echarts.init(document.getElementById(this.eleId));
        var option = {
          title: {
            text: '折线图', // 标题文本
            left: 'center', // 标题位置居中
            textStyle: {
              // 标题字体样式
              fontWeight: 'bold',
              fontSize: 20
            }
          },
          tooltip: {
            trigger: 'axis', // 鼠标悬浮提示类型为折线图
            axisPointer: {
              // 坐标轴指示器配置
              type: 'line', // 指示器类型为线
              lineStyle: {
                color: '#ccc'
              },
              label: {
                backgroundColor: '#ccc'
              }
            }
          },
          legend: {
            data: ['销量'], // 图例为“销量”
            textStyle: {
              // 图例字体样式
              fontSize: 16
            }
          },
          xAxis: {
            name: '日期', // X轴名称
            type: 'category', // X轴类型为类别型
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'], // X轴数据
            axisLine: {
              // X轴轴线样式
              lineStyle: {
                color: '#999'
              }
            },
            axisLabel: {
              // X轴刻度标签样式
              fontSize: 14,
              color: '#666'
            }
          },
          yAxis: {
            name: '销量（万）', // Y轴名称
            type: 'value', // Y轴类型为数值型
            axisLine: {
              // Y轴轴线样式
              lineStyle: {
                color: '#999'
              }
            },
            axisLabel: {
              // Y轴刻度标签样式
              fontSize: 14,
              color: '#666'
            },
            splitLine: {
              // 分隔线样式
              lineStyle: {
                color: ['#ccc'],
                type: 'dashed'
              }
            }
          },
          series: [
            {
              name: '销量', // 数据系列名称
              type: 'line', // 图表类型为折线图
              data: [5, 20, 36, 10, 10, 20, 15], // 数据
              symbolSize: 10, // 数据节点大小
              itemStyle: {
                // 数据节点样式
                color: '#0077FF'
              },
              lineStyle: {
                // 折线样式
                color: '#0077FF',
                width: 2
              },
              areaStyle: {
                // 区域填充样式
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    {
                      offset: 0,
                      color: 'rgba(0, 119, 255, 0.5)'
                    },
                    {
                      offset: 1,
                      color: 'rgba(255, 255, 255, 0.5)'
                    }
                  ]
                }
              }
            }
          ]
        };
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
