<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  const defaultEchdata = {
    xData: ['2016', '2017', '2018', '2019', '2020'],
    yData: [820, 732, 701, 634, 590, 330, 220]
  };
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: '标题'
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start
        let echartEle = echarts.init(document.getElementById(this.eleId));
        const option = {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '0',
            right: '10',
            top: '30',
            bottom: '0',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: this.echdata.xData,
              axisLabel: {
                interval: 0,
                fontSize: 13,
                margin: 10,
                textStyle: {
                  color: '#96C2FF'
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#2B9DF2'
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '(万人)',
              min: 0,
              max: 1000,
              splitNumber: 6,
              nameLocation: 'end',
              nameTextStyle: {
                fontSize: 12,
                color: '#FDFEFF',
                padding: [0, 0, 0, -40]
              },
              splitLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              splitArea: {
                show: false
              },
              axisLabel: {
                fontSize: 13,
                textStyle: {
                  color: '#96C2FF' // 坐标值得具体的颜色
                }
              }
            }
          ],
          series: [
            {
              data: this.echdata.yData,
              type: 'line',
              color: '#12876c',
              symbolSize: 4,
              smooth: false,
              lineStyle: {
                width: 1,
                type: 'solid'
              }
            }
          ]
        };
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
