export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  const defaultEchdata = {\n    xData: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],\n    yData: [150, 230, 224, 218, 135, 147, 260]\n  };\n  const defaultTitle = '标题';\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: defaultTitle\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    watch: {\n      echdata: {\n        handler(newV, oldV) {\n          this.initEchart();\n        }\n      }\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        const option = {\n          grid: {\n            top: 10,\n            bottom: 0,\n            left: 0,\n            containLabel: true\n          },\n          xAxis: {\n            type: 'category',\n            data: this.echdata.xData\n          },\n          yAxis: {\n            type: 'value'\n          },\n          series: [\n            {\n              data: this.echdata.yData,\n              type: 'line'\n            }\n          ]\n        };\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";