<template>
  <div :id="eleId" class="echart-unit" />
</template>

<script>
  import * as echarts from 'echarts';
  //tag1-start
  const defaultEchdata = {
    xData: ['3月', '4月', '5月', '6月', '7月', '8月'],
    yData1: [31865, 24684, 19010, 22136, 18684, 7130],
    yData2: [11934, 12158, 10809, 13562, 13035, 5019],
    yData3: [917, 543, 340, 102, 187, 32]
  };
  //tag1-end
  //tag1B-start
  const defaultTitle = '标题';
  //tag1B-end
  export default {
    name: '',
    components: {},
    props: {
      echdata: {
        type: Object,
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: defaultTitle
      }
    },
    data() {
      return {
        eleId: `echartsLine${Number(
          Math.random()
            .toString()
            .substr(3, 3) + Date.now()
        ).toString(36)}`
      };
    },
    watch: {
      echdata: {
        handler: function() {
          this.initEchart();
        },
        deep: true
      }
    },
    created() {},
    mounted() {
      this.$echarts = echarts;
      this.$nextTick(function() {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start

        var option = {
          title: {
            text: this.titleText,
            top: -2,
            textStyle: {
              color: '#fff', // 字体颜色
              fontSize: '14px', // 字体大小
              fontWeight: 600 // 字体粗细
            }
          },
          legend: {
            // 图例组件，颜色和名字
            top: 0,
            right: 0,
            textStyle: {
              color: 'rgba(255, 255, 255, .5)',
              fontSize: 12
            },
            icon: 'roundRect',
            itemWidth: 16,
            itemHeight: 10,
            itemGap: 5
          },
          tooltip: {
            trigger: 'axis',
            backgroundColor: 'rgba(0, 0, 0, .8)',
            borderColor: 'rgba(0, 0, 0, .8)',
            textStyle: {
              color: '#fff',
              fontSize: 18
            },
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: [
            {
              left: '3%',
              right: '3%',
              bottom: '10',
              top: '40',
              containLabel: true
            }
          ],
          xAxis: {
            type: 'category',
            axisLabel: {
              // color: 'rgba(255, 255, 255, .7)',
              color: '#fff', // 字体颜色
              fontSize: 13,
              interval: 0
            },
            axisLine: {
              lineStyle: {
                color: 'rgba(209, 209, 209, .3)'
              }
            },
            data: this.echdata.xData
          },
          yAxis: [
            {
              type: 'value',
              name: '',
              axisLabel: {
                // color: 'rgba(255, 255, 255, .7)',
                color: '#fff', // 字体颜色
                fontSize: 13,
                fontWeight: 400
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: 'rgba(209, 209, 209, .3)'
                }
              },
              splitLine: {
                show: false
              }
            }
          ],
          series: [
            {
              name: '岗位推荐人次数',
              type: 'line',
              smooth: true,
              showAllSymbol: true,
              symbol: 'circle',
              symbolSize: 8,
              color: '#10D6FE',
              lineStyle: {
                width: 1
                // type: 'dashed'
              },
              label: { show: true },
              areaStyle: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },
                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }
                  ],
                  globalCoord: false
                }
              },
              itemStyle: {
                normal: {
                  label: {
                    show: true,
                    textStyle: {
                      fontSize: 13,
                      fontWeight: 600
                    }
                  }
                }
              },
              data: this.echdata.yData1
            },
            {
              name: '职业指导人次数',
              type: 'line',
              smooth: true,
              showAllSymbol: true,
              symbol: 'circle',
              symbolSize: 8,
              color: '#E7E626',
              lineStyle: {
                width: 1
                // type: 'dashed'
              },
              label: { show: true },
              areaStyle: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    { offset: 0, color: 'rgba(254, 204, 62, .2)' },
                    { offset: 1, color: 'rgba(254, 204, 62, 0)' }
                  ],
                  globalCoord: false
                }
              },
              itemStyle: {
                normal: {
                  label: {
                    show: true,
                    textStyle: {
                      fontSize: 13,
                      fontWeight: 600
                    }
                  }
                }
              },
              data: this.echdata.yData2
            },
            {
              name: '职业培训人次数',
              type: 'line',
              smooth: true,
              showAllSymbol: true,
              symbol: 'circle',
              symbolSize: 8,
              // color: '#5470c6',
              color: '#9599e2',
              lineStyle: {
                width: 1
                // type: 'dashed'
              },
              label: { show: true },
              areaStyle: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    { offset: 0, color: 'rgba(2, 21, 189, .2)' },
                    { offset: 1, color: 'rgba(2, 21, 189, 0)' }
                  ],
                  globalCoord: false
                }
              },
              itemStyle: {
                normal: {
                  label: {
                    show: true,
                    textStyle: {
                      fontSize: 13,
                      fontWeight: 600
                    }
                  }
                }
              },
              data: this.echdata.yData3
            }
          ]
        };
        //
        let echartEle = echarts.init(document.getElementById(this.eleId));
        echartEle.setOption(option, true);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
