export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\" />\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  //tag1-start\n  const defaultEchdata = {\n    xData: ['3月', '4月', '5月', '6月', '7月', '8月'],\n    yData1: [31865, 24684, 19010, 22136, 18684, 7130],\n    yData2: [11934, 12158, 10809, 13562, 13035, 5019],\n    yData3: [917, 543, 340, 102, 187, 32]\n  };\n  //tag1-end\n  //tag1B-start\n  const defaultTitle = '标题';\n  //tag1B-end\n  export default {\n    name: '',\n    components: {},\n    props: {\n      echdata: {\n        type: Object,\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: defaultTitle\n      }\n    },\n    data() {\n      return {\n        eleId: `echartsLine${Number(\n          Math.random()\n            .toString()\n            .substr(3, 3) + Date.now()\n        ).toString(36)}`\n      };\n    },\n    watch: {\n      echdata: {\n        handler: function() {\n          this.initEchart();\n        },\n        deep: true\n      }\n    },\n    created() {},\n    mounted() {\n      this.$echarts = echarts;\n      this.$nextTick(function() {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n\n        var option = {\n          title: {\n            text: this.titleText,\n            top: -2,\n            textStyle: {\n              color: '#fff', // 字体颜色\n              fontSize: '14px', // 字体大小\n              fontWeight: 600 // 字体粗细\n            }\n          },\n          legend: {\n            // 图例组件，颜色和名字\n            top: 0,\n            right: 0,\n            textStyle: {\n              color: 'rgba(255, 255, 255, .5)',\n              fontSize: 12\n            },\n            icon: 'roundRect',\n            itemWidth: 16,\n            itemHeight: 10,\n            itemGap: 5\n          },\n          tooltip: {\n            trigger: 'axis',\n            backgroundColor: 'rgba(0, 0, 0, .8)',\n            borderColor: 'rgba(0, 0, 0, .8)',\n            textStyle: {\n              color: '#fff',\n              fontSize: 18\n            },\n            axisPointer: {\n              // 坐标轴指示器，坐标轴触发有效\n              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'\n            }\n          },\n          grid: [\n            {\n              left: '3%',\n              right: '3%',\n              bottom: '10',\n              top: '40',\n              containLabel: true\n            }\n          ],\n          xAxis: {\n            type: 'category',\n            axisLabel: {\n              // color: 'rgba(255, 255, 255, .7)',\n              color: '#fff', // 字体颜色\n              fontSize: 13,\n              interval: 0\n            },\n            axisLine: {\n              lineStyle: {\n                color: 'rgba(209, 209, 209, .3)'\n              }\n            },\n            data: this.echdata.xData\n          },\n          yAxis: [\n            {\n              type: 'value',\n              name: '',\n              axisLabel: {\n                // color: 'rgba(255, 255, 255, .7)',\n                color: '#fff', // 字体颜色\n                fontSize: 13,\n                fontWeight: 400\n              },\n              axisLine: {\n                show: true,\n                lineStyle: {\n                  color: 'rgba(209, 209, 209, .3)'\n                }\n              },\n              splitLine: {\n                show: false\n              }\n            }\n          ],\n          series: [\n            {\n              name: '岗位推荐人次数',\n              type: 'line',\n              smooth: true,\n              showAllSymbol: true,\n              symbol: 'circle',\n              symbolSize: 8,\n              color: '#10D6FE',\n              lineStyle: {\n                width: 1\n                // type: 'dashed'\n              },\n              label: { show: true },\n              areaStyle: {\n                color: {\n                  type: 'linear',\n                  x: 0,\n                  y: 0,\n                  x2: 0,\n                  y2: 1,\n                  colorStops: [\n                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },\n                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }\n                  ],\n                  globalCoord: false\n                }\n              },\n              itemStyle: {\n                normal: {\n                  label: {\n                    show: true,\n                    textStyle: {\n                      fontSize: 13,\n                      fontWeight: 600\n                    }\n                  }\n                }\n              },\n              data: this.echdata.yData1\n            },\n            {\n              name: '职业指导人次数',\n              type: 'line',\n              smooth: true,\n              showAllSymbol: true,\n              symbol: 'circle',\n              symbolSize: 8,\n              color: '#E7E626',\n              lineStyle: {\n                width: 1\n                // type: 'dashed'\n              },\n              label: { show: true },\n              areaStyle: {\n                color: {\n                  type: 'linear',\n                  x: 0,\n                  y: 0,\n                  x2: 0,\n                  y2: 1,\n                  colorStops: [\n                    { offset: 0, color: 'rgba(254, 204, 62, .2)' },\n                    { offset: 1, color: 'rgba(254, 204, 62, 0)' }\n                  ],\n                  globalCoord: false\n                }\n              },\n              itemStyle: {\n                normal: {\n                  label: {\n                    show: true,\n                    textStyle: {\n                      fontSize: 13,\n                      fontWeight: 600\n                    }\n                  }\n                }\n              },\n              data: this.echdata.yData2\n            },\n            {\n              name: '职业培训人次数',\n              type: 'line',\n              smooth: true,\n              showAllSymbol: true,\n              symbol: 'circle',\n              symbolSize: 8,\n              // color: '#5470c6',\n              color: '#9599e2',\n              lineStyle: {\n                width: 1\n                // type: 'dashed'\n              },\n              label: { show: true },\n              areaStyle: {\n                color: {\n                  type: 'linear',\n                  x: 0,\n                  y: 0,\n                  x2: 0,\n                  y2: 1,\n                  colorStops: [\n                    { offset: 0, color: 'rgba(2, 21, 189, .2)' },\n                    { offset: 1, color: 'rgba(2, 21, 189, 0)' }\n                  ],\n                  globalCoord: false\n                }\n              },\n              itemStyle: {\n                normal: {\n                  label: {\n                    show: true,\n                    textStyle: {\n                      fontSize: 13,\n                      fontWeight: 600\n                    }\n                  }\n                }\n              },\n              data: this.echdata.yData3\n            }\n          ]\n        };\n        //\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        echartEle.setOption(option, true);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";