export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  const defaultEchdata = {\n    xData: ['2016', '2017', '2018', '2019', '2020'],\n    yData: [820, 732, 701, 634, 590, 330, 220]\n  };\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: '标题'\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        const option = {\n          tooltip: {\n            trigger: 'axis',\n            axisPointer: {\n              // 坐标轴指示器，坐标轴触发有效\n              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'\n            }\n          },\n          grid: {\n            left: '0',\n            right: '10',\n            top: '30',\n            bottom: '0',\n            containLabel: true\n          },\n          xAxis: [\n            {\n              type: 'category',\n              data: this.echdata.xData,\n              axisLabel: {\n                interval: 0,\n                fontSize: 13,\n                margin: 10,\n                textStyle: {\n                  color: '#96C2FF'\n                }\n              },\n              axisLine: {\n                show: true,\n                lineStyle: {\n                  color: '#2B9DF2'\n                }\n              }\n            }\n          ],\n          yAxis: [\n            {\n              type: 'value',\n              name: '(万人)',\n              min: 0,\n              max: 1000,\n              splitNumber: 6,\n              nameLocation: 'end',\n              nameTextStyle: {\n                fontSize: 12,\n                color: '#FDFEFF',\n                padding: [0, 0, 0, -40]\n              },\n              splitLine: {\n                show: false\n              },\n              axisTick: {\n                show: false\n              },\n              splitArea: {\n                show: false\n              },\n              axisLabel: {\n                fontSize: 13,\n                textStyle: {\n                  color: '#96C2FF' // 坐标值得具体的颜色\n                }\n              }\n            }\n          ],\n          series: [\n            {\n              data: this.echdata.yData,\n              type: 'line',\n              color: '#12876c',\n              symbolSize: 4,\n              smooth: false,\n              lineStyle: {\n                width: 1,\n                type: 'solid'\n              }\n            }\n          ]\n        };\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";