export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  //tag1-start\n  const defaultEchdata = {\n    xData: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],\n    yData: [150, 230, 224, 218, 135, 147, 260],\n    yData2: [130, 200, 182, 190, 120, 127, 160]\n  };\n  //tag1-end\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    watch: {\n      echdata: {\n        handler: function() {\n          this.dram();\n        },\n        deep: true\n      }\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        const option = {\n          grid: {\n            top: 30,\n            bottom: 0,\n            left: 0,\n            containLabel: true\n          },\n          xAxis: {\n            type: 'category',\n            data: this.echdata.xData\n          },\n          yAxis: {\n            type: 'value'\n          },\n          series: [\n            {\n              data: this.echdata.yData,\n              type: 'line'\n            },\n            {\n              data: this.echdata.yData2,\n              type: 'line' // 在上方显示\n            }\n          ]\n        };\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";