<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  //tag1-start
  const defaultEchdata = {
    xData: ['1月', '2月', '3月', '4月', '5月', '6月'],
    yData: [5, 20, 36, 10, 10, 20]
  };
  //tag1-end
  //tag1B-start
  const defaultTitle = '标题';
  //tag1B-end
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: defaultTitle
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start
        let echartEle = echarts.init(document.getElementById(this.eleId));
        // 折线图配置
        let option = {
          // 头部标题
          title: {
            text: 'ECharts 折线图',
            textStyle: {
              color: '#2992ff', // 文字颜色
              fontSize: 14, // 文字字号
              fontWeight: 'bold' // 文字加粗
            },
            left: 'center' // 标题位置
          },

          // 提示框组件
          tooltip: {
            trigger: 'axis', // 提示框触发源，坐标轴触发
            axisPointer: {
              // 坐标轴指示器
              type: 'cross' // 十字准星指示器
            },
            backgroundColor: '#fff', // 提示框背景色
            borderColor: '#ccc', // 提示框边框颜色
            padding: 10, // 内边距
            textStyle: {
              color: '#333' // 提示框文字颜色
            },
            extraCssText: 'box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);' // 提示框阴影样式
          },

          // 图例组件
          legend: {
            data: ['销量'],
            textStyle: {
              color: '#999' // 图例文字颜色
            },
            right: '2%', // 图例距离容器右侧的距离
            top: '5%', // 图例距离容器顶部的距离
            itemGap: 20 // 图例项之间的距离
          },

          // 网格组件
          grid: {
            top: 50,
            left: '3%', // 网格左侧的距离
            right: '4%', // 网格右侧的距离
            bottom: '10%', // 网格底部的距离
            containLabel: true // 包含坐标轴标签的网格区域
          },

          // X轴
          xAxis: {
            type: 'category', // 类目轴，适用于离散数据
            data: this.echdata.xData,
            axisTick: {
              // 坐标轴刻度
              show: false // 不显示坐标轴刻度
            },
            axisLine: {
              // 坐标轴线
              lineStyle: {
                color: '#ccc' // 坐标轴颜色
              }
            },
            axisLabel: {
              // 坐标轴标签
              color: '#666', // 坐标轴文字颜色
              margin: 10 // 坐标轴文字和轴线之间的距离
            }
          },

          // Y轴
          yAxis: {
            type: 'value', // 数值轴，适用于连续数据
            axisTick: {
              show: false // 不显示坐标轴刻度
            },
            axisLine: {
              show: true,
              // 坐标轴线
              lineStyle: {
                color: '#ccc'
              }
            },
            axisLabel: {
              // 坐标轴标签
              color: '#666',
              margin: 10
            },
            splitLine: {
              // Y轴网格线
              lineStyle: {
                color: '#eee',
                type: 'dashed' // 虚线
              }
            }
          },

          // 数据系列
          series: [
            {
              name: '销量',
              type: 'line',
              smooth: true, // 平滑曲线
              showSymbol: false, // 不显示节点图案
              lineStyle: {
                color: '#ffa000', // 折线颜色
                opacity: 1, // 折线透明度
                width: 1 // 折 线线宽度
              },
              areaStyle: {
                // 折线下方的填充区域
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    // 线性渐变
                    offset: 0,
                    color: '#fff' // 渐变起始颜色
                  },
                  {
                    offset: 1,
                    color: '#ffa000' // 渐变终止颜色
                  }
                ]),
                opacity: 0.2 // 填充区域透明度
              },
              data: this.echdata.yData // 数据
            }
          ],

          // 动画效果
          animationDuration: 1000, // 动画持续时间
          animationEasing: 'cubicOut' // 动画效果，缓入缓出 cubicIn-out
        };
        //
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
