export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  //tag1-start\n  const defaultEchdata = {\n    xData: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],\n    yData: [150, 230, 224, 218, 135, 147, 260]\n  };\n  //tag1-end\n  //tag1B-start\n  const defaultTitle = '标题';\n  //tag1B-end\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: defaultTitle\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        var option = {\n          title: {\n            text: '折线图', // 标题文本\n            left: 'center', // 标题位置居中\n            textStyle: {\n              // 标题字体样式\n              fontWeight: 'bold',\n              fontSize: 20\n            }\n          },\n          tooltip: {\n            trigger: 'axis', // 鼠标悬浮提示类型为折线图\n            axisPointer: {\n              // 坐标轴指示器配置\n              type: 'line', // 指示器类型为线\n              lineStyle: {\n                color: '#ccc'\n              },\n              label: {\n                backgroundColor: '#ccc'\n              }\n            }\n          },\n          legend: {\n            data: ['销量'], // 图例为“销量”\n            textStyle: {\n              // 图例字体样式\n              fontSize: 16\n            }\n          },\n          xAxis: {\n            name: '日期', // X轴名称\n            type: 'category', // X轴类型为类别型\n            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'], // X轴数据\n            axisLine: {\n              // X轴轴线样式\n              lineStyle: {\n                color: '#999'\n              }\n            },\n            axisLabel: {\n              // X轴刻度标签样式\n              fontSize: 14,\n              color: '#666'\n            }\n          },\n          yAxis: {\n            name: '销量（万）', // Y轴名称\n            type: 'value', // Y轴类型为数值型\n            axisLine: {\n              // Y轴轴线样式\n              lineStyle: {\n                color: '#999'\n              }\n            },\n            axisLabel: {\n              // Y轴刻度标签样式\n              fontSize: 14,\n              color: '#666'\n            },\n            splitLine: {\n              // 分隔线样式\n              lineStyle: {\n                color: ['#ccc'],\n                type: 'dashed'\n              }\n            }\n          },\n          series: [\n            {\n              name: '销量', // 数据系列名称\n              type: 'line', // 图表类型为折线图\n              data: [5, 20, 36, 10, 10, 20, 15], // 数据\n              symbolSize: 10, // 数据节点大小\n              itemStyle: {\n                // 数据节点样式\n                color: '#0077FF'\n              },\n              lineStyle: {\n                // 折线样式\n                color: '#0077FF',\n                width: 2\n              },\n              areaStyle: {\n                // 区域填充样式\n                color: {\n                  type: 'linear',\n                  x: 0,\n                  y: 0,\n                  x2: 0,\n                  y2: 1,\n                  colorStops: [\n                    {\n                      offset: 0,\n                      color: 'rgba(0, 119, 255, 0.5)'\n                    },\n                    {\n                      offset: 1,\n                      color: 'rgba(255, 255, 255, 0.5)'\n                    }\n                  ]\n                }\n              }\n            }\n          ]\n        };\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";