export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  //tag1-start\n  const defaultEchdata = {\n    xData: ['2015', '2016', '2017', '2018', '2019', '2020'],\n    yData: [5, 20, 36, 10, 10, 20],\n    yData2: [15, 30, 15, 35, 20, 25]\n  };\n  //tag1-end\n  //tag1B-start\n  const defaultTitle = '标题';\n  //tag1B-end\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        // 配置项\n        var option = {\n          // 标题\n          title: {\n            text: '折线图', // 主标题文本\n            subtext: '示例', // 副标题文本\n            left: 'center' // 标题位置\n          },\n\n          // 图例\n          legend: {\n            data: ['数据一', '数据二'], // 图例数据\n            top: '5%' // 图例位置\n          },\n\n          // 提示框\n          tooltip: {\n            trigger: 'axis'\n          },\n\n          // x 轴\n          xAxis: {\n            type: 'category', // 类目轴\n            data: this.echdata.xData, // x 轴数据\n            axisLine: {\n              // x 轴轴线设置\n              lineStyle: {\n                color: '#999' // 灰色\n              }\n            },\n            axisTick: {\n              // x 轴刻度线设置\n              show: false // 不显示刻度线\n            }\n          },\n\n          // y 轴\n          yAxis: {\n            type: 'value', // 数值轴\n            axisLine: {\n              // y 轴轴线设置\n              show: false // 不显示轴线\n            },\n            axisTick: {\n              // y 轴刻度线设置\n              show: false // 不显示刻度线\n            }\n          },\n\n          // 数据系列\n          series: [\n            {\n              name: '数据一',\n              type: 'line',\n              smooth: true, // 平滑曲线\n              symbol: 'circle', // 折线拐点形状\n              symbolSize: 8, // 折线拐点大小\n              lineStyle: {\n                // 折线颜色和线条样式设置\n                color: '#61a0a8',\n                width: 2 // 折线线宽度\n              },\n              itemStyle: {\n                // 折线拐点样式设置\n                color: '#c23531'\n              },\n              areaStyle: {\n                // 折线下方填充区域\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\n                  {\n                    offset: 0,\n                    color: '#fff'\n                  },\n                  {\n                    offset: 1,\n                    color: '#61a0a8'\n                  }\n                ]),\n                opacity: 0.2\n              },\n              data: this.echdata.yData\n            },\n            {\n              name: '数据二',\n              type: 'line',\n              smooth: true, // 平滑曲线\n              symbol: 'circle', // 折线拐点形状\n              symbolSize: 8, // 折线拐点大小\n              lineStyle: {\n                // 折线颜色和线条样式设置\n                color: '#d48265',\n                width: 2 // 折线线宽度\n              },\n              itemStyle: {\n                // 折线拐点样式设置\n                color: '#91c7ae'\n              },\n              areaStyle: {\n                // 折线下方填充区域\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\n                  {\n                    offset: 0,\n                    color: '#fff'\n                  },\n                  {\n                    offset: 1,\n                    color: '#d48265'\n                  }\n                ]),\n                opacity: 0.2\n              },\n              data: this.echdata.yData2\n            }\n          ],\n\n          // 工具箱\n          toolbox: {\n            feature: {\n              dataView: {\n                // 数据视图\n                show: true, // 显示数据视图\n                readOnly: false // 可编辑\n              },\n              saveAsImage: {\n                // 保存为图片\n                show: true // 显示保存为图片\n              },\n              restore: {\n                // 重置\n                show: true // 显示重置\n              },\n              dataZoom: {\n                //// 数据区域缩放\n                show: true // 显示数据区域缩放\n              },\n              magicType: {\n                // 图表类型切换\n                show: true, // 显示类型切换\n                type: ['line', 'bar'] // 可切换的图表类型\n              },\n              brush: {\n                // 选框组件，用于区域选择\n                show: true // 显示选框组件\n              }\n            },\n            // 工具箱总体设置\n            top: '5%', // 工具箱位置\n            right: '5%', // 工具箱位置\n            orient: 'vertical', // 工具箱图标布局，垂直排布\n            itemGap: 15, // 工具箱中各个图标之间的距离\n            emphasis: {\n              // 工具箱图标高亮样式\n              iconStyle: {\n                borderColor: '#fff',\n                shadowBlur: 10,\n                shadowOffsetX: 0,\n                shadowOffsetY: 0,\n                shadowColor: 'rgba(0, 0, 0, 0.5)'\n              }\n            }\n          }\n        };\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";