<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  //tag1-start
  const defaultEchdata = {
    xData: ['第一季', '第二季', '第三季', '第四季'],
    yData: [50, 20, 40, 35]
  };
  //tag1-end
  //tag1B-start
  const defaultTitle = '标题';
  //tag1B-end
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: defaultTitle
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start
        let echartEle = echarts.init(document.getElementById(this.eleId));
        var option = {
          legend: {
            // 图例组件，颜色和名字
            top: 0,
            right: 0,
            textStyle: {
              color: 'rgba(255, 255, 255, .5)',
              fontSize: 14
            },
            // icon: 'roundRect',
            itemWidth: 16,
            itemHeight: 10,
            itemGap: 5
          },
          tooltip: {
            trigger: 'axis',
            backgroundColor: 'rgba(0, 0, 0, .8)',
            borderColor: 'rgba(0, 0, 0, .8)',
            textStyle: {
              color: '#fff',
              fontSize: 18
            },
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: [
            {
              left: '3%',
              right: '3%',
              bottom: '0',
              top: '20%',
              containLabel: true
            }
          ],
          xAxis: {
            type: 'category',
            axisLabel: {
              color: 'rgba(255, 255, 255, .7)',
              fontSize: 14,
              interval: 0
            },
            axisLine: {
              lineStyle: {
                color: 'rgba(209, 209, 209, .3)'
              }
            },
            data: this.echdata.xData
          },
          yAxis: [
            {
              type: 'value',
              name: '',
              axisLabel: {
                color: 'rgba(255, 255, 255, .7)',
                fontSize: 14
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: 'rgba(209, 209, 209, .3)'
                }
              },
              splitLine: {
                show: false
              }
            }
          ],
          series: [
            {
              name: '参保人数(万人)',
              type: 'line',
              smooth: true,
              showAllSymbol: true,
              symbol: 'circle',
              symbolSize: 4,
              color: '#0ED793',
              itemStyle: {
                normal: {
                  color: '#040B2B',
                  borderColor: '#0ED793',
                  borderWidth: 1
                }
              },
              lineStyle: {
                width: 1,
                color: '#0ED793'
                // type: 'dashed'
              },
              areaStyle: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    { offset: 0, color: 'rgba(2, 251, 189, .2)' },
                    { offset: 1, color: 'rgba(2, 251, 189, 0)' }
                  ],
                  globalCoord: false
                }
              },
              label: {
                show: true,
                fontSize: 14,
                color: '#fff',
                position: 'top'
              },
              data: this.echdata.yData
            }
          ]
        };
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
