<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  //tag1-start
  const defaultEchdata = {
    xData: ['Mon', 'Tue', 'Wed', 'Thu'],
    yData: [150, 230, 224, 218, 135, 147, 260]
  };
  //tag1-end
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start
        let echartEle = echarts.init(document.getElementById(this.eleId));
        const option = {
          grid: {
            top: 30,
            bottom: 0,
            left: 0,
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: this.echdata.xData
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: this.echdata.yData,
              type: 'line'
            }
          ]
        };
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
