export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  //tag1-start\n  const defaultEchdata = {\n    xData: ['1月', '2月', '3月', '4月', '5月', '6月'],\n    yData: [5, 20, 36, 10, 10, 20]\n  };\n  //tag1-end\n  //tag1B-start\n  const defaultTitle = '标题';\n  //tag1B-end\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: defaultTitle\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        // 折线图配置\n        let option = {\n          // 头部标题\n          title: {\n            text: 'ECharts 折线图',\n            textStyle: {\n              color: '#333', // 文字颜色\n              fontSize: 16, // 文字字号\n              fontWeight: 'bold' // 文字加粗\n            },\n            left: 'center' // 标题位置\n          },\n\n          // 提示框组件\n          tooltip: {\n            trigger: 'axis', // 提示框触发源，坐标轴触发\n            axisPointer: {\n              // 坐标轴指示器\n              type: 'cross' // 十字准星指示器\n            },\n            backgroundColor: '#fff', // 提示框背景色\n            borderColor: '#ccc', // 提示框边框颜色\n            padding: 10, // 内边距\n            textStyle: {\n              color: '#333' // 提示框文字颜色\n            },\n            extraCssText: 'box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);', // 提示框阴影样式\n            formatter: function(params) {\n              // 提示框内容\n              let str = `销量：${params[0].data}万`;\n              return str;\n            }\n          },\n\n          // 图例组件\n          legend: {\n            data: ['销量'],\n            textStyle: {\n              color: '#999' // 图例文字颜色\n            },\n            right: '2%', // 图例距离容器右侧的距离\n            top: '5%', // 图例距离容器顶部的距离\n            itemGap: 20 // 图例项之间的距离\n          },\n\n          // 网格组件\n          grid: {\n            left: '3%', // 网格左侧的距离\n            right: '4%', // 网格右侧的距离\n            bottom: '10%', // 网格底部的距离\n            containLabel: true // 包含坐标轴标签的网格区域\n          },\n\n          // X轴\n          xAxis: {\n            type: 'category', // 类目轴，适用于离散数据\n            data: this.echdata.xData,\n            axisTick: {\n              // 坐标轴刻度\n              show: false // 不显示坐标轴刻度\n            },\n            axisLine: {\n              // 坐标轴线\n              lineStyle: {\n                color: '#ccc' // 坐标轴颜色\n              }\n            },\n            axisLabel: {\n              // 坐标轴标签\n              color: '#666', // 坐标轴文字颜色\n              margin: 10 // 坐标轴文字和轴线之间的距离\n            }\n          },\n\n          // Y轴\n          yAxis: {\n            type: 'value', // 数值轴，适用于连续数据\n            axisTick: {\n              show: false // 不显示坐标轴刻度\n            },\n            axisLine: {\n              lineStyle: {\n                color: '#ccc'\n              }\n            },\n            axisLabel: {\n              color: '#666',\n              margin: 10\n            },\n            splitLine: {\n              // Y轴网格线\n              lineStyle: {\n                color: '#eee',\n                type: 'dashed' // 虚线\n              }\n            }\n          },\n\n          // 数据系列\n          series: [\n            {\n              name: '销量',\n              type: 'line',\n              smooth: true, // 平滑曲线\n              showSymbol: false, // 不显示节点图案\n              lineStyle: {\n                color: '#ffa000',\n                width: 2 // 折线线宽度\n              },\n              areaStyle: {\n                // 折线下方填充区域\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\n                  {\n                    offset: 0,\n                    color: '#fff'\n                  },\n                  {\n                    offset: 1,\n                    color: '#ffa000'\n                  }\n                ]),\n                opacity: 0.2\n              },\n              data: this.echdata.yData\n            }\n          ],\n\n          // 工具箱\n          toolbox: {\n            feature: {\n              dataView: {\n                // 数据视图\n                show: true, // 显示数据视图\n                readOnly: false // 可编辑\n              },\n              saveAsImage: {\n                // 保存为图片\n                show: true // 显示保存为图片\n              },\n              restore: {\n                // 重置\n                show: true // 显示重置\n              },\n              magicType: {\n                // 图表类型转换\n                type: ['line', 'bar'] // 可选折线图、柱状图\n              }\n            },\n            right: '5%', // 工具箱距离容器右侧的距离\n            top: '5%' // 工具箱距离容器顶部的距离\n          },\n\n          // 视觉映射组件\n          visualMap: {\n            type: 'piecewise', // 分块型视觉映射组件\n            pieces: [\n              {\n                gt: 0,\n                lte: 10,\n                color: '#d94e5d' // 红色\n              },\n              {\n                gt: 10,\n                lte: 20,\n                color: '#eac736' // 黄色\n              },\n              {\n                gt: 20,\n                lte: 30,\n                color: '#50a3ba' // 蓝色\n              },\n              {\n                gt: 30,\n                color: '#32c5e9' // 深蓝色\n              }\n            ],\n            outOfRange: {\n              color: '#999' // 超出映射范围的颜色\n            },\n            right: '5%', // 视觉映射组件距离容器右侧的距离\n            bottom: '5%', // 视觉映射组件距离容器底部的距离\n            orient: 'horizontal' // 视觉映射组件方向设置\n          },\n\n          // 数据缩放组件\n          // dataZoom: {\n          //   type: 'slider', // 滑动条型数据缩放组\n          //   start: 0, // 数据窗口的起始位置\n          //   end: 100, // 数据窗口的结束位置\n          //   bottom: '2%', // 数据缩放组件距离容器底部的距离\n          //   handleIcon: 'M0,0 v9.7h5 v-9.7z', // 缩放手柄图标\n          //   handleSize: '80%', // 缩放手柄大小\n          //   handleStyle: {\n          //     // 缩放手柄样式\n          //     color: '#ddd'\n          //   },\n          //   backgroundColor: '#f7f7f7', // 滑动条背景色\n          //   fillerColor: '#ddd', // 滑动条填充色\n          //   borderColor: '#eee' // 滑动条边框颜色\n          // },\n\n          // 动画效果配置\n          animationDuration: 1000,\n          animationEasing: 'cubicOut'\n        };\n        // ................\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";