<template>
  <div :id="eleId" class="echart-unit"></div>
</template>

<script>
  import * as echarts from 'echarts';
  //tag1-start
  const defaultEchdata = {
    xData: ['2015', '2016', '2017', '2018', '2019', '2020'],
    yData: [5, 20, 36, 10, 10, 20],
    yData2: [15, 30, 15, 35, 20, 25]
  };
  //tag1-end
  //tag1B-start
  const defaultTitle = '标题';
  //tag1B-end
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        //tag2-start
        let echartEle = echarts.init(document.getElementById(this.eleId));
        // 配置项
        var option = {
          // 标题
          title: {
            text: '折线图', // 主标题文本
            subtext: '示例', // 副标题文本
            left: 'center' // 标题位置
          },

          // 图例
          legend: {
            data: ['数据一', '数据二'], // 图例数据
            top: '5%' // 图例位置
          },

          // 提示框
          tooltip: {
            trigger: 'axis'
          },

          // x 轴
          xAxis: {
            type: 'category', // 类目轴
            data: this.echdata.xData, // x 轴数据
            axisLine: {
              // x 轴轴线设置
              lineStyle: {
                color: '#999' // 灰色
              }
            },
            axisTick: {
              // x 轴刻度线设置
              show: false // 不显示刻度线
            }
          },

          // y 轴
          yAxis: {
            type: 'value', // 数值轴
            axisLine: {
              // y 轴轴线设置
              show: false // 不显示轴线
            },
            axisTick: {
              // y 轴刻度线设置
              show: false // 不显示刻度线
            }
          },

          // 数据系列
          series: [
            {
              name: '数据一',
              type: 'line',
              smooth: true, // 平滑曲线
              symbol: 'circle', // 折线拐点形状
              symbolSize: 8, // 折线拐点大小
              lineStyle: {
                // 折线颜色和线条样式设置
                color: '#61a0a8',
                width: 2 // 折线线宽度
              },
              itemStyle: {
                // 折线拐点样式设置
                color: '#c23531'
              },
              areaStyle: {
                // 折线下方填充区域
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#fff'
                  },
                  {
                    offset: 1,
                    color: '#61a0a8'
                  }
                ]),
                opacity: 0.2
              },
              data: this.echdata.yData
            },
            {
              name: '数据二',
              type: 'line',
              smooth: true, // 平滑曲线
              symbol: 'circle', // 折线拐点形状
              symbolSize: 8, // 折线拐点大小
              lineStyle: {
                // 折线颜色和线条样式设置
                color: '#d48265',
                width: 2 // 折线线宽度
              },
              itemStyle: {
                // 折线拐点样式设置
                color: '#91c7ae'
              },
              areaStyle: {
                // 折线下方填充区域
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#fff'
                  },
                  {
                    offset: 1,
                    color: '#d48265'
                  }
                ]),
                opacity: 0.2
              },
              data: this.echdata.yData2
            }
          ],

          // 工具箱
          toolbox: {
            feature: {
              dataView: {
                // 数据视图
                show: true, // 显示数据视图
                readOnly: false // 可编辑
              },
              saveAsImage: {
                // 保存为图片
                show: true // 显示保存为图片
              },
              restore: {
                // 重置
                show: true // 显示重置
              },
              dataZoom: {
                //// 数据区域缩放
                show: true // 显示数据区域缩放
              },
              magicType: {
                // 图表类型切换
                show: true, // 显示类型切换
                type: ['line', 'bar'] // 可切换的图表类型
              },
              brush: {
                // 选框组件，用于区域选择
                show: true // 显示选框组件
              }
            },
            // 工具箱总体设置
            top: '5%', // 工具箱位置
            right: '5%', // 工具箱位置
            orient: 'vertical', // 工具箱图标布局，垂直排布
            itemGap: 15, // 工具箱中各个图标之间的距离
            emphasis: {
              // 工具箱图标高亮样式
              iconStyle: {
                borderColor: '#fff',
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowOffsetY: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        };
        echartEle.setOption(option);
        // 实时缩放
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
        //tag2-end
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
