export default "<template>\n  <div :id=\"eleId\" class=\"echart-unit\"></div>\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  //tag1-start\n  const defaultEchdata = {\n    xData: ['1月', '2月', '3月', '4月', '5月', '6月'],\n    yData: [5, 20, 36, 10, 10, 20]\n  };\n  //tag1-end\n  //tag1B-start\n  const defaultTitle = '标题';\n  //tag1B-end\n  export default {\n    props: {\n      echdata: {\n        type: [Object, Array],\n        default: () => defaultEchdata\n      },\n      titleText: {\n        type: String,\n        default: defaultTitle\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`,\n        chart: null\n      };\n    },\n    mounted() {\n      this.$nextTick(() => {\n        this.initEchart();\n      });\n    },\n    methods: {\n      initEchart() {\n        //tag2-start\n        let echartEle = echarts.init(document.getElementById(this.eleId));\n        // 折线图配置\n        let option = {\n          // 头部标题\n          title: {\n            text: 'ECharts 折线图',\n            textStyle: {\n              color: '#2992ff', // 文字颜色\n              fontSize: 14, // 文字字号\n              fontWeight: 'bold' // 文字加粗\n            },\n            left: 'center' // 标题位置\n          },\n\n          // 提示框组件\n          tooltip: {\n            trigger: 'axis', // 提示框触发源，坐标轴触发\n            axisPointer: {\n              // 坐标轴指示器\n              type: 'cross' // 十字准星指示器\n            },\n            backgroundColor: '#fff', // 提示框背景色\n            borderColor: '#ccc', // 提示框边框颜色\n            padding: 10, // 内边距\n            textStyle: {\n              color: '#333' // 提示框文字颜色\n            },\n            extraCssText: 'box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);' // 提示框阴影样式\n          },\n\n          // 图例组件\n          legend: {\n            data: ['销量'],\n            textStyle: {\n              color: '#999' // 图例文字颜色\n            },\n            right: '2%', // 图例距离容器右侧的距离\n            top: '5%', // 图例距离容器顶部的距离\n            itemGap: 20 // 图例项之间的距离\n          },\n\n          // 网格组件\n          grid: {\n            top: 50,\n            left: '3%', // 网格左侧的距离\n            right: '4%', // 网格右侧的距离\n            bottom: '10%', // 网格底部的距离\n            containLabel: true // 包含坐标轴标签的网格区域\n          },\n\n          // X轴\n          xAxis: {\n            type: 'category', // 类目轴，适用于离散数据\n            data: this.echdata.xData,\n            axisTick: {\n              // 坐标轴刻度\n              show: false // 不显示坐标轴刻度\n            },\n            axisLine: {\n              // 坐标轴线\n              lineStyle: {\n                color: '#ccc' // 坐标轴颜色\n              }\n            },\n            axisLabel: {\n              // 坐标轴标签\n              color: '#666', // 坐标轴文字颜色\n              margin: 10 // 坐标轴文字和轴线之间的距离\n            }\n          },\n\n          // Y轴\n          yAxis: {\n            type: 'value', // 数值轴，适用于连续数据\n            axisTick: {\n              show: false // 不显示坐标轴刻度\n            },\n            axisLine: {\n              show: true,\n              // 坐标轴线\n              lineStyle: {\n                color: '#ccc'\n              }\n            },\n            axisLabel: {\n              // 坐标轴标签\n              color: '#666',\n              margin: 10\n            },\n            splitLine: {\n              // Y轴网格线\n              lineStyle: {\n                color: '#eee',\n                type: 'dashed' // 虚线\n              }\n            }\n          },\n\n          // 数据系列\n          series: [\n            {\n              name: '销量',\n              type: 'line',\n              smooth: true, // 平滑曲线\n              showSymbol: false, // 不显示节点图案\n              lineStyle: {\n                color: '#ffa000', // 折线颜色\n                opacity: 1, // 折线透明度\n                width: 1 // 折 线线宽度\n              },\n              areaStyle: {\n                // 折线下方的填充区域\n                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [\n                  {\n                    // 线性渐变\n                    offset: 0,\n                    color: '#fff' // 渐变起始颜色\n                  },\n                  {\n                    offset: 1,\n                    color: '#ffa000' // 渐变终止颜色\n                  }\n                ]),\n                opacity: 0.2 // 填充区域透明度\n              },\n              data: this.echdata.yData // 数据\n            }\n          ],\n\n          // 动画效果\n          animationDuration: 1000, // 动画持续时间\n          animationEasing: 'cubicOut' // 动画效果，缓入缓出 cubicIn-out\n        };\n        //\n        echartEle.setOption(option);\n        // 实时缩放\n        window.addEventListener('resize', () => {\n          echartEle.resize();\n        });\n        //tag2-end\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .echart-unit {\n    height: 100%;\n  }\n</style>\n";